import styles from './Header.module.scss'

function Header() {
    return (
        <div className={`d-flex flex-row justify-between align-center mb-35 ${styles.header}`}>

            <div className="d-flex align-center cu-p">
                <img
                    width={62}
                    height={62}
                    src="/img/logo.svg"
                    alt=""
                />
                <div className="ml-5">
                    <h3 className="text-uppercase">CodeCraft</h3>
                    <p><em>-</em></p>
                </div>
            </div>

            <div className="d-flex flex-row">
                <p className={`d-flex align-center justify-center ${styles.link} ${styles.active}`}></p>
                <p className={`d-flex align-center justify-center ${styles.link} ${styles.inactive}`}></p>
                <div className={`d-flex align-center justify-center ${styles.profile}`}>
                    <img width={32} height={32} src="/img/user.svg" alt=" " />
                    <p className="ml-5">user_1210181231313</p>
                    <div className={styles.dropMenu}>
                        <button className='d-flex align-center justify-center cu-p'>
                            <img 
                                src="/img/user.svg" 
                                alt=" " 
                                className='mr-5'
                            />
                            <p></p>
                        </button>
                        <button className={`d-flex align-center justify-center cu-p ${styles.lastButton}`}>
                            <img
                                src="/img/exit.svg"
                                alt=" "
                                className='mr-5'
                            />
                            <p></p>
                        </button>
                    </div>
                </div>
            </div>

            
        </div>
    )
}

export default Header